body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

body{font-family:'Open Sans Condensed', sans-serif;padding:20px 60px}a{text-decoration:none;color:black}*{box-sizing:border-box}

.home-page{display:flex;flex-direction:column;align-items:center;padding:20px 80px}

.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;overflow:hidden}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:0.9}.menu-item.large{height:380px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-position:center;background-size:cover}.menu-item .content{position:absolute;height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid black;background-color:white;opacity:0.7}.menu-item .content .title{font-weight:bold;margin-bottom:6px;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

.collection-preview{display:flex;flex-direction:column;margin-bottom:30px}.collection-preview .title{font-size:28px;margin-bottom:25px}.collection-preview .preview{display:flex;justify-content:space-between}

.collection-item{width:22%;display:flex;flex-direction:column;height:350px;align-items:center;position:relative}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item .footer{width:100%;height:5%;display:flex;justify-content:space-between;font-size:18px}.collection-item .footer .name{width:90%;margin-bottom:15px}.collection-item .add-to-cart-button{width:80%;opacity:0.85;position:absolute;top:255px;display:none}.collection-item:hover .image{opacity:0.8}.collection-item:hover .add-to-cart-button{display:flex}

.button.add-to-cart-button{background-color:white;color:black;border:1px solid black}.button.add-to-cart-button:hover{background-color:black;color:white;border:none}

.button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:"Open Sans Condensed", sans-serif;font-weight:bolder;border:none;cursor:pointer;display:flex;align-items:center;justify-content:center}.button:hover{background-color:white;color:black;border:1px solid black}

.header{height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px}.header .logo-container{height:100%;width:70px;padding:25px}.header .options{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end}.header .options .option{padding:10px 15px;cursor:pointer}

.shopping-cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.shopping-cart-icon .shopping-bag-icon{width:24px;height:24px}.shopping-cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.shopping-cart-dropdown{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:80px;right:0;z-index:5}.shopping-cart-dropdown .cart-items{height:240px;display:flex;flex-direction:column;overflow:auto}.shopping-cart-dropdown .cart-items .empty-message{font-size:18px;margin:50px auto}.shopping-cart-dropdown button{margin-top:auto}

.shopping-cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.shopping-cart-item img{width:30%}.shopping-cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding-left:20px}

.sign-in-sign-up-page{width:850px;display:flex;justify-content:space-between;margin:30px auto}

.form-input{position:relative;margin:45px 0}.form-input .input{background:white none;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.form-input .input:focus{outline:none}.form-input .input:focus ~ .label{top:-14px;font-size:12px;color:#000}.form-input .input[type='password']{letter-spacing:0.3em}.form-input .label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.form-input .label.shrink{top:-14px;font-size:12px;color:#000}

.sign-in{width:380px;display:flex;flex-direction:column}.sign-in .title{margin:10px 0}.sign-in .buttons{display:flex;justify-content:space-between}

.button.google-sign-in-button{background-color:#4285f4;color:white}.button.google-sign-in-button:hover{background-color:#357ae8;border:none}

.sign-up{display:flex;flex-direction:column;width:380px}.sign-up .title{margin:10px 0}.sign-up .error{color:red;margin-bottom:10px}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:50px auto}.checkout-page .checkout-header{width:100%;height:40px;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23px}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .total{margin-top:30px;margin-left:auto;font-size:35px}

